//     
export default function scroll(yesSelector, noSelector, targetElem) {
    const btnYes = document.querySelector(yesSelector),
        btnNo = document.querySelector(noSelector);

    const speed = 0.4; //  

    btnYes.addEventListener("click", (e) => {
        e.preventDefault();
        scrollLogic();
    });

    btnNo.addEventListener("click", (e) => {
        e.preventDefault();
        scrollLogic();
    });

    function scrollLogic() {
        let widthTop = document.documentElement.scrollTop, //    
            toBlock = document
                .querySelector(targetElem)
                .getBoundingClientRect().top, //        
            start = null; //    

        requestAnimationFrame(step); //  

        function step(time) {
            //    
            if (start === null) {
                //    
                start = time;
            }

            let progress = time - start,
                r =
                    toBlock < 0
                        ? Math.max(
                              widthTop - progress / speed,
                              widthTop + toBlock
                          )
                        : Math.min(
                              widthTop + progress / speed,
                              widthTop + toBlock
                          ); // -             

            document.documentElement.scrollTo(0, r); //    r

            if (r != widthTop + toBlock) {
                //        ,    
                requestAnimationFrame(step);
            }
        }
    }
}
